.header {
    min-height: 5vmin;
    display:flex;
    float: none;
    background-color: rgba(49, 113, 134);
    color: white;
    /* min-width: 100%; */
    width: 100%;
}

.header-nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-width: 100%;
    /* margin-left: auto;
    margin-right: auto; 
    width: 10em;
    max-width: 100em;  */
}

.logoLinkStyle {
    color: white;
    text-decoration: none;
}

.nav-links {
    width: 55%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}

.linkStyle {
    color: white;
}

.MenuLinkStyle {
    color: black;
    list-style: none;
    text-decoration: none;
}
  
.App-logo {
    height: 8vmin;
    pointer-events: all;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

/* @media only screen and (max-width: 360px) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
} */

@keyframes slidein {
    from { transform: scaleX(0); }
    to   { transform: scaleX(1); }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.drop-menu {
    background-color: rgb(50, 142, 173);
    border-radius: 5px;
    color: white;
}